import React, { useContext } from 'react'
import { observer } from 'mobx-react'
import Store from '../Store'

import {
  SampleColumn,
  SampleButton,
  SampleSelected,
  SampleOutOfStock,
  SampleName,
  SampleDescription,
  SampleImageContainer,
  SampleImage,
} from '../styles/SampleChoice.module.scss'


export const Sample = props => {
  const store = useContext(Store)
  const sample = props.sample
  return (
    <div className={SampleColumn}>
      <div className={[
          SampleButton,
          (store.selected === sample.id ? SampleSelected : ''),
          (sample.inStock ? '' : SampleOutOfStock),
        ].join(' ')}
        key={sample.id}
        onClick={() => {
          if(sample.inStock) {
            store.updateChoice(sample.id)
          }
        }}
      >
        <div className={SampleImageContainer}>
          <img className={SampleImage} src={sample.img} alt={sample.name}/>
        </div>
        <p className={SampleName}>{sample.name}</p>
        {sample.inStock &&
        <div>
          <input id={sample.id} className="input-radio" type="radio" name="sample" value={sample.id} checked={store.selected === sample.id} readOnly={true}></input>
          <label htmlFor={sample.id}></label>
          <p className={SampleDescription}>{sample.description}</p>
        </div>
        }
        {!sample.inStock &&
          <p className={SampleName} style={{color: '#e31837'}}>Out of Stock</p>
        }
      </div>
    </div>
  )
}

export default observer(Sample)
