import React, { useContext } from 'react'
import { observer } from 'mobx-react'
import Store from './Store'
import Sample from './components/Sample'

import {
  ChoiceColumn,
  Subtitle,
  LoadingImage,
  ImageColumn,
} from './styles/SampleChoice.module.scss'

export const SampleChoice = () => {
  const store = useContext(Store)

  const radios = document.getElementsByName('probox')
  for (var i=0; i < radios.length; i++) {
    radios[i].addEventListener('change', store.loadChoices)
  }

  if(!store.dataLoaded && !store.dataLoading) {
    store.loadChoices()
  }

  return (
    <div>
      <div className={ImageColumn}>
        <label>
          {(!store.dataLoaded || store.canChoose) && <span>Choose Your Sample</span>}
          {store.dataLoaded && !store.canChoose && <span>Check Back Later</span>}
        </label>
        {store.canChoose &&
          <div>
            <p className={Subtitle}>
              Choose a sample for your {store.month} box.*
            </p>
            <p className={Subtitle}>
              Your sample choice will be recorded each time you click on a radio button.
            </p>
            <p className={Subtitle}>
              The other samples in your PRO Box will be chosen for you based on your selected box type.
            </p>
          </div>
        }
        {store.dataLoaded && !store.canChoose &&
          <p className={Subtitle}>Itâ€™s not currently your time to select a sample for your box, but stay tuned - the ability to choose a sample for your box is available the month prior to your scheduled box shipment</p>
        }
      </div>
      <div className={ChoiceColumn}>
        {!store.dataLoaded &&
          <img
            className={LoadingImage}
            src="https://assets.bulubox.com/loading.gif"
            alt="loading"
            width="100"
          />
        }
        {store.dataLoaded && store.canChoose &&
          <div>
            <div>
              {store.choices.map(choice => (
                <Sample sample={choice} key={choice.id}/>
              ))}
            </div>
          </div>
        }
      </div>
      {store.canChoose &&
        <p style={{fontSize: '12px', marginBottom: 0}}>*Sample selections are subject to availability.</p>
      }
      <script src="https://cdnjs.cloudflare.com/ajax/libs/babel-core/5.6.15/browser-polyfill.min.js"></script>
    </div>
  )
}

export default observer(SampleChoice)
