import { createContext } from 'react'
import { decorate, observable, action, computed } from 'mobx'
import 'whatwg-fetch'

const API_URL = window.location.hostname === 'localhost' || window.location.hostname === 'gnc.staging.bulubox.com'
  ? 'https://gnc-dev.bulubox.com/choice/'
  : '/on/demandware.store/Sites-GNC-Site/default/Bulu-Connect'


export class SampleChoiceStore {
  dataLoaded = false
  dataLoading = false

  month = null
  choices = []
  selected = 0

  set(key, value) {
    this[key] = value
  }

  get customerId() {
    return window.user.loyaltyID
  }

  get date() {
    if(window.expirationDate !== undefined) {
      return window.expirationDate
    }
    return '2020-05-28'
  }

  get type() {
    let type = 1
    try {
      type = document.querySelectorAll("input[name=probox]:checked")[0].value
    } catch {}
    return type
  }

  get canChoose() {
    return this.dataLoaded && this.choices.length > 0
  }

  post = async id => {
    let data = {
      customer_id: this.customerId,
      box_type: this.type,
      date: this.date,
      id: this.selected
    }
    return await fetch(API_URL, {
      method: 'POST',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data)
    })
    .then(res => res.json())
    .catch(console.log)
  }

  updateChoice = async id => {
    // Don't spam if they click the same one
    if(id === this.selected) {
      return
    }
    this.set('selected', id)
    this.post(id)
  }

  loadChoices = async () => {
    this.set('dataLoading', true)
    this.set('dataLoaded', false)
    this.set('selected', 0)

    if (window.user === undefined) {
      setTimeout(this.loadChoices, 100)
    } else {
      let data = await this.post()
      if(data.samples && data.month) {
        this.set('month', data.month)
        this.set('choices', data.samples)
        if(data.selected) {
          this.set('selected', data.selected);
        }
      }
      this.set('dataLoaded', true)
      this.set('dataLoading', false)
    }
  }
}

decorate(SampleChoiceStore, {
  dataLoaded: observable,
  dataLoading: observable,
  month: observable,
  choices: observable,
  selected: observable,

  type: computed,
  customerId: computed,
  date: computed,
  canChoose: computed,

  set: action,
  get: action,
  updateChoice: action,
  loadChoices: action,
})

export default createContext(new SampleChoiceStore())
